
import XHeader from 'vux/src/components/x-header/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Radio from 'vux/src/components/radio/index.vue'

export default {
components: {
XHeader,
ViewBox,
Group,
XTextarea,
XButton,
Radio
},
data() {
return {
feedback_text: '',
feedList: ["bug反馈", "意见反馈", "ui问题", "其他"],
feedMethod: "bug反馈",
}
},
methods: {
onEvent(event) {
console.log('on', event)
},

toSubmit() {
var feedback_text = this.feedback_text
console.log(feedback_text);
console.log(this.feedMethod);
if(this.feedMethod == "bug反馈") {
var feedback_type = 0
}
if(this.feedMethod == "意见反馈") {
var feedback_type = 1
}
if(this.feedMethod == "ui问题") {
var feedback_type = 2
}
if(this.feedMethod == "其他") {
var feedback_type = 3
}
if(feedback_text.length <= 0) {
this.$vux.toast.show({
text: "请输入内容",
type: "text",
position: "middle"
});
return false;
}
this.$post(
"feedback/add", {
add: {
feedback_type: feedback_type,
feedback_info: feedback_text
}
},
res => {
console.log(res);
if(res = 1) {
this.$vux.toast.show({
text: "提交成功",
type: "text",
position: "middle"
});
var _this = this;
setTimeout(function() {
_this.$router.back(-1);
}, 1500)
}
if(res != 1) {
this.$vux.toast.show({
text: "网络错误",
type: "text",
position: "middle"
});
}

}
);

}
}
}



